.ExpressionChooser-dropdown {
  position: absolute;
  list-style: none;
  margin: 2px 0 0 0;
  padding: 0;
  max-height: 195px;
  overflow-y: scroll;
  background: #eee;
  color: #333;
}

.ExpressionChooser-dropdown li {
  padding: 5px 0;
  outline: 1px solid #333;
}

.ExpressionChooser-dropdown li.ExpressionChooser-dropdown-selected {
  background: #bbb;
}
