.Editor {
  /* border: 1px solid #aaa; */
  font-size: 18px;
  background: #333;
  color: #eee;
  padding: 1em;
}

.Editor:focus {
  outline-style: none;
  box-shadow: none;
}

.Editor-text-edit-input {
  border: 0;
  padding: 0;
  font-size: inherit;
  font-family: inherit;
  background: #eee;
  color: #333;
}

.Editor-theme-controls {
  float: right;
  font-size: 0.8em;
}

.Editor-theme-controls select {
  font-size: inherit;
  font-family: inherit;
  margin-left: 0.3em;
}
